import React, { useEffect, useRef } from "react";
import { refreshWidgetData } from "./services/api/refreshService";
import useToast from "./hooks/useToast";
import useDroppableArea from "./hooks/useDroppableArea";
import { MSG_REFRESH_ERROR, MSG_REFRESH_SUCCESS } from "./utils/toastMessages";
import store from "./store"; // âœ… Import Redux store directly

const WidgetLifecycle = () => {
  const { handleDrop } = useDroppableArea();
  const { showSuccessToast, showErrorToast } = useToast();

  const initialDraggedDataRef = useRef(null); // âœ… Store latest initialDraggedData here

  console.log("---[WidgetLifecycle]--- starts");

  useEffect(() => {
    console.log("[WidgetLifecycle] ðŸ“Œ Checking Redux store for initialDraggedData...");

    const state = store.getState();
    const initialDraggedData = state.droppedObject.droppedObjectData.initialDraggedData;

    if (!initialDraggedData?.data?.items?.length) {
      console.warn("[WidgetLifecycle] ðŸš¨ No initialDraggedData available yet.");
    } else {
      initialDraggedDataRef.current = initialDraggedData; // âœ… Store latest Redux value in ref
      console.log("[WidgetLifecycle] âœ… Stored initialDraggedData:", initialDraggedDataRef.current);
    }
  }, []); // âœ… Run once on mount to store the latest value

  const hasOnRefreshRegistered = useRef(false); // âœ… Prevent duplicate `onRefresh` registrations

  useEffect(() => {
    if (!window.widget) return;

    if (hasOnRefreshRegistered.current) {
      console.warn("[WidgetLifecycle] â³ onRefresh is already registered. Ignoring duplicate.");
      return;
    }

    
    hasOnRefreshRegistered.current = true; // âœ… Prevent duplicate registrations
    console.log("[WidgetLifecycle] ðŸ“Œ Registering `onRefresh` event...");
    const onRefresh = async () => {
      console.log("[WidgetLifecycle] ðŸ”„ `onRefresh` triggered!");

      const latestState = store.getState(); // âœ… Fetch latest Redux state dynamically
      const latestDraggedData = latestState.droppedObject.droppedObjectData.initialDraggedData;

      if (!latestDraggedData?.data?.items?.length) {
        console.error("[WidgetLifecycle] âš ï¸ `initialDraggedData` is missing or invalid:", latestDraggedData);
        return; // âœ… Prevents execution if data is invalid
      }

      initialDraggedDataRef.current = latestDraggedData; // âœ… Update ref with latest data

      const objectId = latestDraggedData.data.items[0]?.objectId;
      const objectType = latestDraggedData.data.items[0]?.objectType;

      if (!objectId || !objectType) {
        console.error("[WidgetLifecycle] âŒ Missing `objectId` or `objectType`. Skipping refresh.");
        return;
      }

      try {
        console.log("[WidgetLifecycle] ðŸš€ Refreshing widget with latest data...");
        await refreshWidgetData(latestDraggedData.data.items, handleDrop);
        showSuccessToast(MSG_REFRESH_SUCCESS);
      } catch (error) {
        console.error("[WidgetLifecycle] âŒ Error during refresh:", error);
        showErrorToast(MSG_REFRESH_ERROR);
      }
    };
    const originalOnRefresh = window.widget.dispatchEvent;

    window.widget.dispatchEvent = function (event, ...args) {
      if (event === "onRefresh") {
        console.warn("[DEBUG] `onRefresh` is being triggered externally! Hereâ€™s the stack trace:");
        console.trace();
      }
      return originalOnRefresh.apply(this, [event, ...args]);
    };
    
    console.log("[WidgetLifecycle] âœ… `onRefresh` event registered successfully");
  }, []); // âœ… Ensures this runs **only once**

  return null;
};

export default WidgetLifecycle;
